<template>
    <div class="firework hidden">
        <div v-if="!showGame" class="packet-container height-auto hidden">
            <div class="packet-box">
                <van-icon
                    class="packet"
                    size="5em"
                    name="https://api.iconify.design/mingcute:red-packet-fill.svg?color=%23ff4e4c"
                    @click="handleShowGame"
                />
                <div><span class="target">{{ who }}</span></div>
            </div>
        </div>
        <template v-else>
            <van-icon
                class="emitter"
                size="5em"
                name="https://api.iconify.design/twemoji:firecracker.svg?color=%23f4be49"
                @click="handleFire"
            />
            <van-icon
                v-if="fireworkList.length <= 0"
                class="tip"
                size="1.5em"
                name="https://api.iconify.design/tabler:hand-finger-left.svg?color=%23f4be49"
            />
            <canvas ref="firework" />
            <div class="bless">
                <span class="who target">{{ who }}</span><span class="to target">{{ to }}:</span>2025!
                <div>{{ bless }}</div>
            </div>
        </template>
        <audio ref="music" :src="bgMusicPath" autoplay loop />
    </div>
</template>
<script>
import { deleteFromList } from '@/utils/deleteFromList'
import { Firework } from './firework'
import { getRandom } from '@/utils/getRandom'

export default {
    name: 'Firework',
    data() {
        return {
            showGame: false,
            fireworkList: [],
            who: '',
            defaultWho: '',
            to: '',
            defaultTo: '',
            bgMusicPath: require('../../../assets/music/bg-falling-you.mp3'),
            blessList: [
                ', !',
                ', !',
                '!',
                ', , , !',
                ', !',
                ', !',
                ', !',
                '!',
                '!',
                '!',
                '!',
                '!',
                '!'
            ],
            bless: ''
        }
    },
    beforeRouteEnter(to, from, next) {
        next(vm => {
            vm.showGame = from.path.indexOf('/home') > -1
        })
    },
    watch: {
        showGame(val) {
            if (val) {
                this.$nextTick(() => {
                    this.initFirework()
                    this.handleFire()
                })
            }
        }
    },
    mounted() {
        const { who = '', to = '', bg = '' } = this.$route.query
        this.who = who || this.defaultWho
        this.to = to || this.defaultTo
        if (bg) {
            this.bgMusicPath = require('../../../assets/music/bg-new-year.mp3')
        }
    },
    methods: {
        handleShowGame() {
            this.$refs.music.play()
            this.showGame = true
        },
        initFirework() {
            const canvas = this.$refs.firework
            const width = window.innerWidth
            const height = window.innerHeight
            canvas.width = width
            canvas.height = height

            canvas.style.width = width + 'px'
            canvas.style.height = height + 'px'

            const ctx = canvas.getContext('2d')

            // 
            const draw = () => {
                // 
                ctx.fillStyle = 'rgba(0, 0, 0, 0.3)'
                ctx.fillRect(0, 0, width, height)

                ctx.save()

                // 
                ctx.translate(0, height)
                ctx.scale(1, -1)

                const list = [...this.fireworkList]
                list.forEach(firework => {
                    firework.update()
                    if (firework.isEnd()) {
                        deleteFromList(this.fireworkList, firework)
                    }
                })

                ctx.restore()

                requestAnimationFrame(draw)
            }
            draw()
        },
        // 
        createFireworkColor() {
            const colorList = [
                '#ff7875',
                '#f5222d',
                '#ff9c6e',
                '#fa541c',
                '#ffc069',
                '#fa8c16',
                '#ffd666',
                '#faad14',
                '#fff566',
                '#fadb14',
                '#d3f261',
                '#a0d911',
                '#95de64',
                '#52c41a',
                '#5cdbd3',
                '#13c2c2',
                '#69b1ff',
                '#1677ff',
                '#85a5ff',
                '#2f54eb',
                '#b37feb',
                '#722ed1',
                '#ff85c0',
                '#eb2f96'
            ]
            return colorList[Math.floor(Math.random() * colorList.length)]
        },
        handleFire() {
            const firework = new Firework({
                target: this.$refs.firework,
                color: this.createFireworkColor()
            })
            this.fireworkList.push(firework)
            firework.launch()
            this.bless = this.blessList[Math.floor(getRandom(0, this.blessList.length - 1))]
        }
    }
}
</script>
<style lang="scss" scoped>
@import './index.scss';
</style>
